import React, { ReactElement } from "react";
import * as HouseAPI from "../../api/houses";
import * as SchoolAPI from "../../api/school";
import { House, HouseDTO } from "../../api/houses/model.interface";
import { PageHeaderContext } from "../../context/layout";
import AppBtn from "../../components/form/AppBtn";
import AppInput from "../../components/form/AppInput";
import { Grid } from "@material-ui/core";
import withPageLayout from "../../hocs/withPageLayout";
import { withRouter } from "react-router";
import AppInputSelect from "../../components/form/AppInputSelect";

interface HouseEditProps {
  HouseDetails: House;
}
const HouseEdit = (props: any) => {
  const [name, setName] = React.useState("");
  const [colourCode, setColorCode] = React.useState("");
  const [schools, setSchools] = React.useState([]);
  const [schoolId, setSchoolId] = React.useState(0);

  const LayoutContext = React.useContext(PageHeaderContext);

  React.useEffect(() => {
    if (props.match.params.id) {
      getHouse(props.match.params.id);
    }
  }, [props.match.params.id]);

  const getHouse = async (id: number) => {
    try {
      const schoolsList: any = await SchoolAPI.get();
      setSchools(schoolsList);
      const houseDetails: any | House = await HouseAPI.details(id);
      setName(houseDetails.name);
      setColorCode(houseDetails.colour_code);
      setSchoolId(parseInt(houseDetails.school_id));
    } catch (error) {
      console.log({ error });
    }
  };
  const updateHouse = async () => {
    props.setShowLoading(true);

    const houseDTO: HouseDTO = {
      name,
      colour_code: colourCode,
      school_id: 1,
    };
    const updatedHouse = await HouseAPI.put(
      parseInt(props.match.params.id),
      houseDTO
    );
    props.setShowLoading(false);
  };

  React.useEffect(() => {
    LayoutContext.setTitle("Edit House");
  }, []);
  return (
    <>
      <Grid>
        <AppInput
          label="Name"
          onChangeText={(name: string) => {
            setName(name);
          }}
          value={name}
          placeholder="Enter Name"
          prefix={null}
          suffix={null}
        />
        <AppInput
          label="Color"
          onChangeText={(colour_code: string) => setColorCode(colour_code)}
          value={colourCode}
          type="color"
          placeholder="Color"
          prefix={null}
          suffix={null}
        />
        <AppInputSelect
          value={schoolId}
          handleChange={(v: number) => setSchoolId(v)}
          data={schools}
          label="School"
        />

        <AppBtn block title="Update" onClick={() => updateHouse()} />
      </Grid>
    </>
  );
};
export default withRouter(withPageLayout(HouseEdit));
